import React from 'react'
import { toTitle } from '../utils'

function AddButton({singular}){
    return ( <a className='add-new' href={`/${singular}/new?editing=true`}> Add </a>)
}

export default class List extends React.Component {

    static propTypes = {
        ItemView: React.PropTypes.func.isRequired,
        type: React.PropTypes.object.isRequired,
        plural: React.PropTypes.string,
        listControls: React.PropTypes.array,
        items: React.PropTypes.array
    }

    render(){
        let { listControls=[AddButton], items=[], ItemView, plural, children, actions, ...controlProps } = this.props
        return children ?
            React.cloneElement(children, {actions}) : (
            <div className={`${plural.toLowerCase()} relational list`}>
                <div className="header">
                    <h1 className="title">{toTitle(plural)}</h1>
                    <div className="controls">
                        {listControls.map(Control => (<Control key={Control.name} actions={actions} {...controlProps}/>))}
                    </div>
                </div>
                <div className="items">
                    {items.map(i => (
                        <div key={i._id} className={`${singular.toLowerCase()} item`}>
                            <ItemView {...i}/>
                        </div>
                    ))}
                </div>
            </div>
        )
    }
}



